$bgColor: #37003c;
$white: #ffffff;
$brandGreen: #00ff87;

body {
  font-family: 'Archivo', sans-serif;
}

.main-widget {
  width: 100%;
  height: 100vh;
  background-color: $bgColor;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: $brandGreen;
  h1 {
    padding: 0;
    margin: 0;
    font-size: 100px;
  }
}
